import React from "react";

// Ce composant permet de grer l'affichage d'une formation 
const Formation = ({formation}) => {

  //Si les caractres de la date sont la balise <p> c'est qu'il n'y a pas de date, la formation  donc t frm et ne doit pas tre affich
  if(!formation.date.includes("<p>"))
    return(
<div style={{border : "solid black 2px ", margin : "2px", marginTop : "8px"}}>
  Domaine : {formation.domain} <br/> Titre : {formation.title} <br/> Date : {formation.date}
  
        
</div>
    )
  
  else return null;
}
export default Formation;